form {
    margin: 2rem;
}

input {
    padding: 1rem;
    border-radius: 10px;
}

.specific {
    outline: none;
}

.interesting {
    display: flex;
}

.icones {
    display: flex;
}
.inputsize {
    display: flex;
}


.inpt-disabled {
    background-color: rgba(114, 112, 114, 0.275);
    cursor: not-allowed;
}


.inpt-error {
    border: tomato 2px solid;
    outline: none;

}

.inpt-error > label {
  color: red;
}

/* .inpt-helperText {
    border: tomato 2px solid;
} */
p {
    color: red;
}

.inpt-error:hover {
    border: black 2px solid;
}

.inpt-error:focus {
    border: springgreen 2px solid;
}

.inpt-helperText:focus {
  border: black 1px solid;
}

.specific:hover {
    border: rgb(12, 145, 168) 2px solid;
}

.specific:focus {
    border: rgb(228, 28, 211) 2px solid;
}



.inpt-sm {
    padding: 0.75rem  2rem;
}

.inpt-md {
    padding: 1rem 3rem;
}

.inpt-text {
   border: yellowgreen 2px solid;
}
.inpt-text:hover,
.inpt-text:focus {
  border: black 2px solid;
}

.inpt-fullwidth {
    padding-right: 90%;
    border: black 1px solid;
}








